/* SWITCH */
@media only screen and (max-width:767px) {
    #mobileMenu { display:block; } 
    #desktopMenu { display:none; }
}
@media only screen and (min-width:768px) {
    #mobileMenu { display:none; } 
    #desktopMenu { display:block; }
}
@media only screen and (min-width:820px) {
    body #desktopMenu #logo { width:87% !important; }
}
@media only screen and (min-width:1024px) {
    body #desktopMenu #logo { width:88% !important; }
}
@media only screen and (min-width:1280px) {
    body #desktopMenu #logo { width:90% !important; }
}


/* DESKTOP */
body #desktopMenu #logo { width:85%; }
body #desktopMenu #logo img { width:15%; height:auto; } 
body #header #desktopMenu .nav-link { padding-left:0.25em; padding-right:0.25em; color:#575151; }
body #header #desktopMenu .textlink { padding-top: 30px; display: inline-block; }

/* MOBILE */
body #mobileMenu #logo { width:30%; display:inline-block;}
body #mobileMenu #logo img { width:50%; height:auto;}
body #mobileContainer { color:#575151; }
body #mobileMenu .nav-link { font-size:1.2em; padding-right:5%; }
footer { background:#eee8e8; padding:10px; }
footer span { display:inline-block; padding: 0 5px; }
footer svg { margin:0 5px; }
footer span, footer a { color:#575151; font-size:12px; }
/*indiv */
li { list-style-type: none; }

@media only screen and (max-width:767px) {
    img { width:100%; }
}

.about_page { padding:15px; }

body .col { padding:0 !important; margin:15px 0px 0px 15px !important}
body .row { padding-left:15px !important; }

@media only screen and (max-width:767px) {
   main img, main .col { width:90vw !important; height: auto !important; }
   body .row > div { min-width: 100vw !important; max-height:50vh; overflow:hidden; margin-bottom:15px; }
   
}
@media only screen and (min-width:768px) {
    .work_page_thumbnails_container{ overflow:hidden; height:25vh; margin-top:15px; }
    body .row { margin-right:15px;}
    .projdesc { margin-top:15px; }
}

body .projdesc { padding:15px; background:#ebe9e9; }
body .projdesc p { white-space: pre-wrap; }
body .projImg  { width:100%; margin-top:15px; }
.work_page { background: #f3eeee; padding: 20px 0; }
.workThumb img:hover { background-color:black; opacity: 0.3; }

