/* framework */
html { overflow-y:scroll; }
body { background:#fff; }
body a, body a:hover { text-decoration: none; }
a:focus, a.active { color:#0056b3 !important; }
main { padding-bottom: 7%; }
.boldthis { font-weight:bold}
body div, body p, body span { color:#5a5656; }
